body {
  background-color: #d3d3d3;
  margin: 0;
  font-family: Roboto, sans-serif;
}

:root {
  --blue-color: #00acdc;
}

.header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.nav {
  display: block;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__link {
  text-transform: uppercase;
  text-align: center;
  color: #000;
  align-items: center;
  height: 60px;
  font-size: 12px;
  font-weight: 500;
  line-height: 14px;
  text-decoration: none;
  display: flex;
}

.logo {
  align-self: center;
  height: 40px;
}

.is-active {
  color: var(--blue-color);
  position: relative;
}

.nav__item:not(:first-child) {
  margin-left: 20px;
}

.nav__item :hover {
  color: var(--blue-color);
}

.is-active:after {
  content: "";
  background-color: var(--blue-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

